import goodsHeader from './goodsHeader.vue'
import popupSpec from '../../components/popupSpec.vue'
export default{
	components:{
		goodsHeader,
		popupSpec
	},
	data(){
		return{
			isKeep:false,//收藏
			goodsData:{
				//轮播图
				swiperList:[],
				//码数
				spec:[],
				//评论【 图片 评论内容 评论名字 】
				comment:[{face:"",content:"",username:""}],
				descriptionStr:""
			},
			goodsInfo:{
				name:"",
				price:"",
				number:"",
				spec:""
			},
			currentSwiper: 0, // 轮播图下标
			spaceInfo: {
				showSpace: false
			}
		}
	},
	onLoad(option){
		this.goodsInfo = JSON.parse(option.goodsInfo)
		//console.log(this.goodsInfo)
		this.initData()
	},
	methods:{	
		add(){
			this.goodsInfo.number++
		},
		sub(){
			if(this.goodsInfo.number<=1){
				return
			}
			this.goodsInfo.number--
		},
		setSelectSpec(item){
			//console.log(item)
			//选择规格
			this.goodsInfo.spec = item
		},
		hideSpec(){
			this.spaceInfo.showSpace = false
		},
		initData(){
			this.request({
				//请求轮播图 码数 评论【 图片 评论内容 评论名字 】
				url:'https://www.wudongming.top/uni/goods',
				success:((res)=>{
					this.goodsData = res.data;	
					//console.log(this.goodsData)
				})
			})
		},
		swiperChange(event){
			//console.log(event.detail.current)
			this.currentSwiper = event.detail.current
		},
		handleRatings(){
			//本地存储
			uni.setStorageSync('comments',this.goodsData.comment)
			uni.navigateTo({
				url:"./ratings"
			})
		},
		keep(){
			this.isKeep = !this.isKeep
			if(this.isKeep){
				uni.showToast({
					icon:"success",
					title:"已收藏"
				})
			}
		},
		joinCart(){
			//console.log(this.goodsInfo)

			//存储到本地存储里
			//先去本地存储中取
			uni.getStorage({
				key:"goodsList",
				success:((res)=>{
					//拿数据
					//console.log("加入成功")
					//console.log(res.data)
					let goodsList = res.data
					//查找商品是否存在
					let isExist = false
					goodsList.forEach(goods => {
						if(goods.goods_id == this.goodsInfo.goods_id && goods.spec == this.goodsInfo.spec){
							// 如果存在 修改商品商量
							goods.number += this.goodsInfo.number;
							isExist = true;
						}
					})
					if(!isExist){
						goodsList.push(this.goodsInfo)
						//更新本地存储
						this.setGoodsList(goodsList)
					}
					
				}),
				fail:(err => {//没有得到数据
				//console.log("加入失败")
				 let goodsList = [];
				 goodsList.push(this.goodsInfo)
				 //往本地存储中存储数据
				 this.setGoodsList(goodsList)
				})
			})
		},
		setGoodsList(goodsList){
			//console.log("存储到本地存储中")
			//存储到本地存储中
			uni.setStorage({
				key:"goodsList",
				data:goodsList,
				success:function(){
					uni.showToast({
						icon:"success",
						title:"添加购物车成功"
					})
				}
			})
		},
		bandleBuy(){
			//console.log(this.goodsInfo)
			let tempList = []
			tempList.push(this.goodsInfo)
			uni.setStorage({
				key:"confirmList",
				data:tempList,
				success:()=>{
					uni.navigateTo({
						url:"../order/confirm"
					})
				}
			})
		}
	}
}
